body {
  margin: 0px;
  font-family: 'Segoe UI', Tahoma, Geneva, Verdana, sans-serif;
}

.App {
  display: grid;
  grid-template-columns: 300px auto;
}

.creature-input-form {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-direction: column;
      flex-direction: column;
  -ms-flex-align: center;
      align-items: center;
}

.card-grid {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-wrap: wrap;
      flex-wrap: wrap;
}

.creature-card {
  display: grid;
  grid-template-rows: 100px 100px auto;
  width: 300px;
  height: 400px;
  -ms-flex-align: center;
      align-items: center;
  border: red solid 1px;
  position: relative;
}

.creature-card h2 {
  text-align: center;
  -webkit-user-select: none;
     -moz-user-select: none;
      -ms-user-select: none;
          user-select: none;
  cursor: pointer;
}

.editable-input {
  width: 200px;
  margin: auto;
  text-align: center;
}

.editable-input input {
  text-align: center;
}

.health-tracker {
  text-align: center;
}

.health-tracker button {
  width: 70px;
  height: 50px;
  margin-left: 25px;
  margin-right: 25px;
  background-color: green;
  outline: none;
  border: none;
}

.condition-tracker {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-wrap: wrap;
      flex-wrap: wrap;
  padding: 0px;
  margin: 0px;
  list-style: none;
}

.condition-tracker li {
  margin: 4px;
  cursor: pointer;
  -webkit-user-select: none;
     -moz-user-select: none;
      -ms-user-select: none;
          user-select: none;
}

.close-card {
  position: absolute;
  top:0;
  right:0;
  font-weight: bold;
  width: 30px;
  height: 30px;
  text-align: center;
  -webkit-user-select: none;
     -moz-user-select: none;
      -ms-user-select: none;
          user-select: none;
  cursor: pointer;
}

.edit-card {
  position: absolute;
  top:0;
  left:0;
  font-weight: bold;
  width: 30px;
  height: 30px;
  text-align: center;
  -webkit-user-select: none;
     -moz-user-select: none;
      -ms-user-select: none;
          user-select: none;
  cursor: pointer;
}


